// $primary: $primary;
$primary: #515151;
// $secondary: #47b2e4;
$secondary: #E36835;

body {
    font-family: "Open Sans", sans-serif;
    color: #444444;
  }

  a {
    color: $secondary;
    text-decoration: none;

    &:hover {
      color: darken($color: $secondary, $amount: 5);
      text-decoration: none;
    }
  }

  h1, h2, h3, h4, h5, h6 {
    font-family: "Jost", sans-serif;
  }

  .text-color1 {color: $primary;}
  .text-color2 {color: $secondary;}

  /*--------------------------------------------------------------
  # Preloader
  --------------------------------------------------------------*/

  #preloader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    overflow: hidden;
    background: $primary;

    &:before {
      content: "";
      position: fixed;
      top: calc(50% - 30px);
      left: calc(50% - 30px);
      border: 6px solid $primary;
      border-top-color: #fff;
      border-bottom-color: #fff;
      border-radius: 50%;
      width: 60px;
      height: 60px;
      -webkit-animation: animate-preloader 1s linear infinite;
      animation: animate-preloader 1s linear infinite;
    }
  }

  @-webkit-keyframes animate-preloader {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  @keyframes animate-preloader {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  /*--------------------------------------------------------------
  # Back to top button
  --------------------------------------------------------------*/

  .back-to-top {
    position: fixed;
    visibility: hidden;
    opacity: 0;
    right: 15px;
    bottom: 15px;
    z-index: 996;
    background: $secondary;
    width: 40px;
    height: 40px;
    border-radius: 50px;
    transition: all 0.4s;

    i {
      font-size: 24px;
      color: #fff;
      line-height: 0;
    }

    &:hover {
      background: #6bc1e9;
      color: #fff;
    }

    &.active {
      visibility: visible;
      opacity: 1;
    }
  }

  /*--------------------------------------------------------------
  # Header
  --------------------------------------------------------------*/

  #header {
    transition: all 0.5s;
    z-index: 997;
    padding: 15px 0;

    &.header-scrolled, &.header-inner-pages {
      background: rgba($primary, 0.9);
    }

    .logo {
      font-size: 30px;
      margin: 0;
      padding: 0;
      line-height: 1;
      font-weight: 500;
      letter-spacing: 2px;
      text-transform: uppercase;

      a {
        color: #fff;
      }

      img {
        max-height: 40px;
      }
    }
    .logo-img {
        border-radius: 8px;
    }
  }

  /*--------------------------------------------------------------
  # Navigation Menu
  --------------------------------------------------------------*/
  /**
  * Desktop Navigation
  */

  .navbar {
    padding: 0;

    ul {
      margin: 0;
      padding: 0;
      display: flex;
      list-style: none;
      align-items: center;
    }

    li {
      position: relative;
    }

    a {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px 0 10px 30px;
      font-size: 15px;
      font-weight: 500;
      color: #fff;
      white-space: nowrap;
      transition: 0.3s;

      &:focus {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px 0 10px 30px;
        font-size: 15px;
        font-weight: 500;
        color: #fff;
        white-space: nowrap;
        transition: 0.3s;
      }

      i, &:focus i {
        font-size: 12px;
        line-height: 0;
        margin-left: 5px;
      }

      &:hover {
        color: $secondary;
      }
    }

    .active {
      color: $secondary;

      &:focus {
        color: $secondary;
      }
    }

    li:hover > a {
      color: $secondary;
    }

    .getstarted {
      padding: 8px 20px;
      margin-left: 30px;
      border-radius: 50px;
      color: #fff;
      font-size: 14px;
      border: 2px solid $secondary;
      font-weight: 600;

      &:focus {
        padding: 8px 20px;
        margin-left: 30px;
        border-radius: 50px;
        color: #fff;
        font-size: 14px;
        border: 2px solid $secondary;
        font-weight: 600;
      }

      &:hover, &:focus:hover {
        color: #fff;
        background: $secondary;
      }
    }

    .dropdown {
      ul {
        display: block;
        position: absolute;
        left: 14px;
        top: calc(100% + 30px);
        margin: 0;
        padding: 10px 0;
        z-index: 99;
        opacity: 0;
        visibility: hidden;
        background: #fff;
        box-shadow: 0px 0px 30px rgba(127, 137, 161, 0.25);
        transition: 0.3s;
        border-radius: 4px;

        li {
          min-width: 200px;
        }

        a {
          padding: 10px 20px;
          font-size: 14px;
          text-transform: none;
          font-weight: 500;
          color: #0c3c53;

          i {
            font-size: 12px;
          }

          &:hover {
            color: $secondary;
          }
        }

        .active:hover, li:hover > a {
          color: $secondary;
        }
      }

      &:hover > ul {
        opacity: 1;
        top: 100%;
        visibility: visible;
      }

      .dropdown {
        ul {
          top: 0;
          left: calc(100% - 30px);
          visibility: hidden;
        }

        &:hover > ul {
          opacity: 1;
          top: 0;
          left: 100%;
          visibility: visible;
        }
      }
    }
  }

  @media (max-width: 1366px) {
    .navbar .dropdown .dropdown {
      ul {
        left: -90%;
      }

      &:hover > ul {
        left: -100%;
      }
    }
  }

  /**
  * Mobile Navigation
  */

  .mobile-nav-toggle {
    color: #fff;
    font-size: 28px;
    cursor: pointer;
    display: none;
    line-height: 0;
    transition: 0.5s;

    &.bi-x {
      color: #fff;
    }
  }

  @media (max-width: 991px) {
    .mobile-nav-toggle {
      display: block;
    }

    .navbar ul {
      display: none;
    }
  }

  .navbar-mobile {
    position: fixed;
    overflow: hidden;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba($primary, 0.9);
    transition: 0.3s;
    z-index: 999;

    .mobile-nav-toggle {
      position: absolute;
      top: 15px;
      right: 15px;
    }

    ul {
      display: block;
      position: absolute;
      top: 55px;
      right: 15px;
      bottom: 15px;
      left: 15px;
      padding: 10px 0;
      border-radius: 10px;
      background-color: #fff;
      overflow-y: auto;
      transition: 0.3s;
    }

    a {
      padding: 10px 20px;
      font-size: 15px;
      color: $primary;

      &:focus {
        padding: 10px 20px;
        font-size: 15px;
        color: $primary;
      }

      &:hover {
        color: $secondary;
      }
    }

    .active, li:hover > a {
      color: $secondary;
    }

    .getstarted {
      margin: 15px;
      color: $primary;

      &:focus {
        margin: 15px;
        color: $primary;
      }
    }

    .dropdown {
      ul {
        position: static;
        display: none;
        margin: 10px 20px;
        padding: 10px 0;
        z-index: 99;
        opacity: 1;
        visibility: visible;
        background: #fff;
        box-shadow: 0px 0px 30px rgba(127, 137, 161, 0.25);

        li {
          min-width: 200px;
        }

        a {
          padding: 10px 20px;

          i {
            font-size: 12px;
          }

          &:hover {
            color: $secondary;
          }
        }

        .active:hover, li:hover > a {
          color: $secondary;
        }
      }

      > .dropdown-active {
        display: block;
      }
    }
  }

  /*--------------------------------------------------------------
  # Hero Section
  --------------------------------------------------------------*/

  #hero {
    width: 100%;
    height: 80vh;
    background: $primary;

    .container {
      padding-top: 72px;
    }

    h1 {
      margin: 0 0 10px 0;
      font-size: 48px;
      font-weight: 700;
      line-height: 56px;
      color: #fff;
    }

    h2 {
      color: rgba(255, 255, 255, 0.6);
      margin-bottom: 50px;
      font-size: 24px;
    }

    .btn-get-started {
      font-family: "Jost", sans-serif;
      font-weight: 500;
      font-size: 16px;
      letter-spacing: 1px;
      display: inline-block;
      padding: 10px 28px 11px 28px;
      border-radius: 50px;
      transition: 0.5s;
      margin: 10px 0 0 0;
      color: #fff;
      background: $secondary;

      &:hover {
        background: darken($secondary, 5);
      }
    }

    .btn-watch-video {
      font-size: 16px;
      display: flex;
      align-items: center;
      transition: 0.5s;
      margin: 10px 0 0 25px;
      color: #fff;
      line-height: 1;

      i {
        line-height: 0;
        color: #fff;
        font-size: 32px;
        transition: 0.3s;
        margin-right: 8px;
      }

      &:hover i {
        color: $secondary;
      }
    }

    .animated {
      animation: up-down 2s ease-in-out infinite alternate-reverse both;
    }
  }

  .hero-img {
    text-align: center;

    img {
      max-height: 300px;
    }
  }

  @media (max-width: 991px) {
    #hero {
      height: 100vh;
      text-align: center;

      .animated {
        -webkit-animation: none;
        animation: none;
      }

      .hero-img {
        text-align: center;

        img {
          width: 50%;
        }
      }
    }
  }

  @media (max-width: 768px) {
    #hero {
      h1 {
        font-size: 28px;
        line-height: 36px;
      }

      h2 {
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 30px;
      }

      .hero-img img {
        width: 70%;
      }
    }
  }

  @media (max-width: 575px) {
    #hero {
      .hero-img img {
        width: 80%;
      }

      .btn-get-started {
        font-size: 16px;
        padding: 10px 24px 11px 24px;
      }
    }
  }

  @-webkit-keyframes up-down {
    0% {
      transform: translateY(10px);
    }

    100% {
      transform: translateY(-10px);
    }
  }

  @keyframes up-down {
    0% {
      transform: translateY(10px);
    }

    100% {
      transform: translateY(-10px);
    }
  }

  /*--------------------------------------------------------------
  # Sections General
  --------------------------------------------------------------*/

  section {
    padding: 60px 0;
    overflow: hidden;
  }

  .section-bg {
    // background-color: #f3f5fa;
    background-color: rgba($primary, 0.05);
  }

  .section-title {
    text-align: center;
    padding-bottom: 30px;

    h2 {
      font-size: 32px;
      font-weight: bold;
      text-transform: uppercase;
      margin-bottom: 20px;
      padding-bottom: 20px;
      position: relative;
      color: $primary;

      &::before {
        content: "";
        position: absolute;
        display: block;
        width: 120px;
        height: 1px;
        background: #ddd;
        bottom: 1px;
        left: calc(50% - 60px);
      }

      &::after {
        content: "";
        position: absolute;
        display: block;
        width: 40px;
        height: 3px;
        background: $secondary;
        bottom: 0;
        left: calc(50% - 20px);
      }
    }

    p {
      margin-bottom: 0;
    }
  }

  /*--------------------------------------------------------------
  # Clients
  --------------------------------------------------------------*/

  .clients {
    padding: 12px 0;
    text-align: center;

    img {
      max-width: 45%;
      transition: all 0.4s ease-in-out;
      display: inline-block;
      padding: 15px 0;
      filter: grayscale(100);

      &:hover {
        filter: none;
        transform: scale(1.1);
      }
    }
  }

  @media (max-width: 768px) {
    .clients img {
      max-width: 40%;
    }
  }

  /*--------------------------------------------------------------
  # About Us
  --------------------------------------------------------------*/

  .about .content {
    h3 {
      font-weight: 600;
      font-size: 26px;
    }

    ul {
      list-style: none;
      padding: 0;

      li {
        padding-left: 28px;
        position: relative;

        + li {
          margin-top: 10px;
        }
      }

      i {
        position: absolute;
        left: 0;
        top: 2px;
        font-size: 20px;
        color: $secondary;
        line-height: 1;
      }
    }

    p:last-child {
      margin-bottom: 0;
    }

    .btn-learn-more {
      font-family: "Poppins", sans-serif;
      font-weight: 500;
      font-size: 14px;
      letter-spacing: 1px;
      display: inline-block;
      padding: 12px 32px;
      border-radius: 4px;
      transition: 0.3s;
      line-height: 1;
      color: $secondary;
      -webkit-animation-delay: 0.8s;
      animation-delay: 0.8s;
      margin-top: 6px;
      border: 2px solid $secondary;

      &:hover {
        background: $secondary;
        color: #fff;
        text-decoration: none;
      }
    }
  }

  /*--------------------------------------------------------------
  # Why Us
  --------------------------------------------------------------*/

  .why-us {
    .content {
      padding: 60px 100px 0 100px;

      h3 {
        font-weight: 400;
        font-size: 34px;
        color: $primary;
      }

      h4 {
        font-size: 20px;
        font-weight: 700;
        margin-top: 5px;
      }

      p {
        font-size: 15px;
        color: #848484;
      }
    }

    .img {
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center center;
    }

    .accordion-list {
      padding: 0 100px 60px 100px;

      ul {
        padding: 0;
        list-style: none;
      }

      li {
        + li {
          margin-top: 15px;
        }

        padding: 20px;
        background: #fff;
        border-radius: 4px;
      }

      a {
        display: block;
        position: relative;
        font-family: "Poppins", sans-serif;
        font-size: 16px;
        line-height: 24px;
        font-weight: 500;
        padding-right: 30px;
        outline: none;
        cursor: pointer;
      }

      span {
        color: $secondary;
        font-weight: 600;
        font-size: 18px;
        padding-right: 10px;
      }

      i {
        font-size: 24px;
        position: absolute;
        right: 0;
        top: 0;
      }

      p {
        margin-bottom: 0;
        padding: 10px 0 0 0;
      }

      .icon-show {
        display: none;
      }

      a.collapsed {
        color: #343a40;

        &:hover {
          color: $secondary;
        }

        .icon-show {
          display: inline-block;
        }

        .icon-close {
          display: none;
        }
      }
    }
  }

  @media (max-width: 1024px) {
    .why-us {
      .content, .accordion-list {
        padding-left: 0;
        padding-right: 0;
      }
    }
  }

  @media (max-width: 992px) {
    .why-us {
      .img {
        min-height: 400px;
      }

      .content {
        padding-top: 30px;
      }

      .accordion-list {
        padding-bottom: 30px;
      }
    }
  }

  @media (max-width: 575px) {
    .why-us .img {
      min-height: 200px;
    }
  }

  /*--------------------------------------------------------------
  # Skills
  --------------------------------------------------------------*/

  .skills {
    .content {
      h3 {
        font-weight: 700;
        font-size: 32px;
        color: $primary;
        font-family: "Poppins", sans-serif;
      }

      ul {
        list-style: none;
        padding: 0;

        li {
          padding-bottom: 10px;
        }

        i {
          font-size: 20px;
          padding-right: 4px;
          color: $secondary;
        }
      }

      p:last-child {
        margin-bottom: 0;
      }
    }

    .progress {
      height: 60px;
      display: block;
      background: none;
      border-radius: 0;

      .skill {
        padding: 0;
        margin: 0 0 6px 0;
        text-transform: uppercase;
        display: block;
        font-weight: 600;
        font-family: "Poppins", sans-serif;
        color: $primary;

        .val {
          float: right;
          font-style: normal;
        }
      }
    }

    .progress-bar-wrap {
      background: #e8edf5;
      height: 10px;
    }

    .progress-bar {
      width: 1px;
      height: 10px;
      transition: 0.9s;
      background-color: #4668a2;
    }
  }

  /*--------------------------------------------------------------
  # Services
  --------------------------------------------------------------*/

  .services .icon-box {
    box-shadow: 0px 0 25px 0 rgba(0, 0, 0, 0.1);
    padding: 50px 30px;
    transition: all ease-in-out 0.4s;
    background: #fff;

    .icon {
      margin-bottom: 10px;

      i {
        color: $secondary;
        font-size: 36px;
        transition: 0.3s;
      }
    }

    h4 {
      font-weight: 500;
      margin-bottom: 15px;
      font-size: 24px;

      a {
        color: $primary;
        transition: ease-in-out 0.3s;
      }
    }

    p {
      line-height: 24px;
      font-size: 14px;
      margin-bottom: 0;
    }

    &:hover {
      transform: translateY(-10px);

      h4 a {
        color: $secondary;
      }
    }
  }

  /*--------------------------------------------------------------
  # Cta
  --------------------------------------------------------------*/

  .cta {
    background: linear-gradient(rgba(darken($secondary, 30), 0.9), rgba(darken($secondary, 30), 0.9)), url("/img/frontend_images/cta-bg.jpg") fixed center center;
    background-size: cover;
    padding: 120px 0;

    h3 {
      color: #fff;
      font-size: 28px;
      font-weight: 700;
    }

    p {
      color: rgba(255, 255, 255, 0.8);
    }

    .cta-btn {
      font-family: "Jost", sans-serif;
      font-weight: 500;
      font-size: 16px;
      letter-spacing: 1px;
      display: inline-block;
      padding: 12px 40px;
      border-radius: 50px;
      transition: 0.5s;
      margin: 10px;
      border: 2px solid rgb(255, 255, 255);
      color: rgb(255, 255, 255);

      &:hover {
        background: $secondary;
        border: 2px solid $secondary;
      }
    }
  }

  @media (max-width: 1024px) {
    .cta {
      background-attachment: scroll;
    }
  }

  @media (min-width: 769px) {
    .cta .cta-btn-container {
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
  }

  /*--------------------------------------------------------------
  # Portfolio
  --------------------------------------------------------------*/

  .portfolio {
    #portfolio-flters {
      list-style: none;
      margin-bottom: 20px;

      li {
        cursor: pointer;
        display: inline-block;
        margin: 10px 5px;
        font-size: 15px;
        font-weight: 500;
        line-height: 1;
        color: #444444;
        transition: all 0.3s;
        padding: 8px 20px;
        border-radius: 50px;
        font-family: "Poppins", sans-serif;

        &:hover, &.filter-active {
          background: $secondary;
          color: #fff;
        }
      }
    }

    .portfolio-item {
      margin-bottom: 30px;

      .portfolio-img {
        overflow: hidden;

        img {
          transition: all 0.6s;
        }
      }

      .portfolio-info {
        opacity: 0;
        position: absolute;
        left: 15px;
        bottom: 0;
        z-index: 3;
        right: 15px;
        transition: all 0.3s;
        background: rgba(55, 81, 126, 0.8);
        padding: 10px 15px;

        h4 {
          font-size: 18px;
          color: #fff;
          font-weight: 600;
          color: #fff;
          margin-bottom: 0px;
        }

        p {
          color: #f9fcfe;
          font-size: 14px;
          margin-bottom: 0;
        }

        .preview-link, .details-link {
          position: absolute;
          right: 40px;
          font-size: 24px;
          top: calc(50% - 18px);
          color: #fff;
          transition: 0.3s;
        }

        .preview-link:hover {
          color: $secondary;
        }

        .details-link {
          &:hover {
            color: $secondary;
          }

          right: 10px;
        }
      }

      &:hover {
        .portfolio-img img {
          transform: scale(1.15);
        }

        .portfolio-info {
          opacity: 1;
        }
      }
    }
  }

  /*--------------------------------------------------------------
  # Portfolio Details
  --------------------------------------------------------------*/

  .portfolio-details {
    padding-top: 40px;

    .portfolio-details-slider {
      img {
        width: 100%;
      }

      .swiper-pagination {
        margin-top: 20px;
        position: relative;

        .swiper-pagination-bullet {
          width: 12px;
          height: 12px;
          background-color: #fff;
          opacity: 1;
          border: 1px solid $secondary;
        }

        .swiper-pagination-bullet-active {
          background-color: $secondary;
        }
      }
    }

    .portfolio-info {
      padding: 30px;
      box-shadow: 0px 0 30px rgba(55, 81, 126, 0.08);

      h3 {
        font-size: 22px;
        font-weight: 700;
        margin-bottom: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid #eee;
      }

      ul {
        list-style: none;
        padding: 0;
        font-size: 15px;

        li + li {
          margin-top: 10px;
        }
      }
    }

    .portfolio-description {
      padding-top: 30px;

      h2 {
        font-size: 26px;
        font-weight: 700;
        margin-bottom: 20px;
      }

      p {
        padding: 0;
      }
    }
  }

  /*--------------------------------------------------------------
  # Team
  --------------------------------------------------------------*/

  .team .member {
    position: relative;
    box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);
    padding: 30px;
    border-radius: 5px;
    background: #fff;
    transition: 0.5s;

    .pic {
      overflow: hidden;
      width: 180px;
      border-radius: 50%;

      img {
        transition: ease-in-out 0.3s;
      }
    }

    &:hover {
      transform: translateY(-10px);
    }

    .member-info {
      padding-left: 30px;
    }

    h4 {
      font-weight: 700;
      margin-bottom: 5px;
      font-size: 20px;
      color: $primary;
    }

    span {
      display: block;
      font-size: 15px;
      padding-bottom: 10px;
      position: relative;
      font-weight: 500;

      &::after {
        content: "";
        position: absolute;
        display: block;
        width: 50px;
        height: 1px;
        background: #cbd6e9;
        bottom: 0;
        left: 0;
      }
    }

    p {
      margin: 10px 0 0 0;
      font-size: 14px;
    }

    .social {
      margin-top: 12px;
      display: flex;
      align-items: center;
      justify-content: flex-start;

      a {
        transition: ease-in-out 0.3s;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50px;
        width: 32px;
        height: 32px;
        background: #eff2f8;

        i {
          color: $primary;
          font-size: 16px;
          margin: 0 2px;
        }

        &:hover {
          background: $secondary;

          i {
            color: #fff;
          }
        }

        + a {
          margin-left: 8px;
        }
      }
    }
  }

  /*--------------------------------------------------------------
  # Pricing
  --------------------------------------------------------------*/

  .pricing {
    .row {
      padding-top: 40px;
    }

    .box {
      padding: 60px 40px;
      box-shadow: 0 3px 20px -2px rgba(20, 45, 100, 0.1);
      background: #fff;
      height: 100%;
      border-top: 4px solid #fff;
      border-radius: 5px;
    }

    h3 {
      font-weight: 500;
      margin-bottom: 15px;
      font-size: 20px;
      color: $primary;
    }

    h4 {
      font-size: 48px;
      color: $primary;
      font-weight: 400;
      font-family: "Jost", sans-serif;
      margin-bottom: 25px;

      sup {
        font-size: 28px;
      }

      span {
        color: $secondary;
        font-size: 18px;
        display: block;
      }
    }

    ul {
      padding: 20px 0;
      list-style: none;
      color: #999;
      text-align: left;
      line-height: 20px;

      li {
        padding: 10px 0 10px 30px;
        position: relative;
      }

      i {
        color: #28a745;
        font-size: 24px;
        position: absolute;
        left: 0;
        top: 6px;
      }

      .na {
        color: #ccc;

        i {
          color: #ccc;
        }

        span {
          text-decoration: line-through;
        }
      }
    }

    .buy-btn {
      display: inline-block;
      padding: 12px 35px;
      border-radius: 50px;
      color: $secondary;
      transition: none;
      font-size: 16px;
      font-weight: 500;
      font-family: "Jost", sans-serif;
      transition: 0.3s;
      border: 1px solid $secondary;

      &:hover {
        background: $secondary;
        color: #fff;
      }
    }

    .featured {
      border-top-color: $secondary;

      .buy-btn {
        background: $secondary;
        color: #fff;

        &:hover {
          background: #23a3df;
        }
      }
    }
  }

  @media (max-width: 992px) {
    .pricing .box {
      max-width: 60%;
      margin: 0 auto 30px auto;
    }
  }

  @media (max-width: 767px) {
    .pricing .box {
      max-width: 80%;
      margin: 0 auto 30px auto;
    }
  }

  @media (max-width: 420px) {
    .pricing .box {
      max-width: 100%;
      margin: 0 auto 30px auto;
    }
  }

  /*--------------------------------------------------------------
  # Frequently Asked Questions
  --------------------------------------------------------------*/

  .faq .faq-list {
    padding: 0 100px;

    ul {
      padding: 0;
      list-style: none;
    }

    li {
      + li {
        margin-top: 15px;
      }

      padding: 20px;
      background: #fff;
      border-radius: 4px;
      position: relative;
    }

    a {
      display: block;
      position: relative;
      font-family: "Poppins", sans-serif;
      font-size: 16px;
      line-height: 24px;
      font-weight: 500;
      padding: 0 30px;
      outline: none;
      cursor: pointer;
    }

    .icon-help {
      font-size: 24px;
      position: absolute;
      right: 0;
      left: 20px;
      color: $secondary;
    }

    .icon-show, .icon-close {
      font-size: 24px;
      position: absolute;
      right: 0;
      top: 0;
    }

    p {
      margin-bottom: 0;
      padding: 10px 0 0 0;
    }

    .icon-show {
      display: none;
    }

    a.collapsed {
      color: $primary;
      transition: 0.3s;

      &:hover {
        color: $secondary;
      }

      .icon-show {
        display: inline-block;
      }

      .icon-close {
        display: none;
      }
    }
  }

  @media (max-width: 1200px) {
    .faq .faq-list {
      padding: 0;
    }
  }

  /*--------------------------------------------------------------
  # Contact
  --------------------------------------------------------------*/

  .contact {
    .info {
      border-top: 3px solid $secondary;
      border-bottom: 3px solid $secondary;
      padding: 30px;
      background: #fff;
      width: 100%;
      box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.1);

      i {
        font-size: 20px;
        color: $secondary;
        float: left;
        width: 44px;
        height: 44px;
        background: #e7f5fb;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50px;
        transition: all 0.3s ease-in-out;
      }

      h4 {
        padding: 0 0 0 60px;
        font-size: 22px;
        font-weight: 600;
        margin-bottom: 5px;
        color: $primary;
      }

      p {
        padding: 0 0 10px 60px;
        margin-bottom: 20px;
        font-size: 14px;
        color: #6182ba;
      }

      .email p {
        padding-top: 5px;
      }

      .social-links {
        padding-left: 60px;

        a {
          font-size: 18px;
          display: inline-block;
          background: #333;
          color: #fff;
          line-height: 1;
          padding: 8px 0;
          border-radius: 50%;
          text-align: center;
          width: 36px;
          height: 36px;
          transition: 0.3s;
          margin-right: 10px;

          &:hover {
            background: $secondary;
            color: #fff;
          }
        }
      }

      .email:hover i, .address:hover i, .phone:hover i {
        background: $secondary;
        color: #fff;
      }
    }

    .php-email-form {
      width: 100%;
      border-top: 3px solid $secondary;
      border-bottom: 3px solid $secondary;
      padding: 30px;
      background: #fff;
      box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.12);

      .form-group {
        padding-bottom: 8px;
      }

      .validate {
        display: none;
        color: red;
        margin: 0 0 15px 0;
        font-weight: 400;
        font-size: 13px;
      }

      .error-message {
        display: none;
        color: #fff;
        background: #ed3c0d;
        text-align: left;
        padding: 15px;
        font-weight: 600;

        br + br {
          margin-top: 25px;
        }
      }

      .sent-message {
        display: none;
        color: #fff;
        background: #18d26e;
        text-align: center;
        padding: 15px;
        font-weight: 600;
      }

      .loading {
        display: none;
        background: #fff;
        text-align: center;
        padding: 15px;

        &:before {
          content: "";
          display: inline-block;
          border-radius: 50%;
          width: 24px;
          height: 24px;
          margin: 0 10px -6px 0;
          border: 3px solid #18d26e;
          border-top-color: #eee;
          -webkit-animation: animate-loading 1s linear infinite;
          animation: animate-loading 1s linear infinite;
        }
      }

      .form-group {
        margin-bottom: 20px;
      }

      label {
        padding-bottom: 8px;
      }

      input, textarea {
        border-radius: 0;
        box-shadow: none;
        font-size: 14px;
        border-radius: 4px;
      }

      input:focus, textarea:focus {
        border-color: $secondary;
      }

      input {
        height: 44px;
      }

      textarea {
        padding: 10px 12px;
      }

      button[type=submit] {
        background: $secondary;
        border: 0;
        padding: 12px 34px;
        color: #fff;
        transition: 0.4s;
        border-radius: 50px;

        &:hover {
          background: darken($secondary, 5);
        }
      }
    }
  }

  @-webkit-keyframes animate-loading {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  @keyframes animate-loading {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  /*--------------------------------------------------------------
  # Breadcrumbs
  --------------------------------------------------------------*/

  .breadcrumbs {
    padding: 15px 0;
    background: #f3f5fa;
    min-height: 40px;
    margin-top: 72px;

    h2 {
      font-size: 28px;
      font-weight: 600;
      color: $primary;
    }

    ol {
      display: flex;
      flex-wrap: wrap;
      list-style: none;
      padding: 0 0 10px 0;
      margin: 0;
      font-size: 14px;

      li + li {
        padding-left: 10px;

        &::before {
          display: inline-block;
          padding-right: 10px;
          color: #4668a2;
          content: "/";
        }
      }
    }
  }

  @media (max-width: 992px) {
    .breadcrumbs {
      margin-top: 68px;
    }
  }

  /*--------------------------------------------------------------
  # Footer
  --------------------------------------------------------------*/

  #footer {
    font-size: 14px;
    background: $primary;

    .footer-newsletter {
      padding: 50px 0;
      background: #f3f5fa;
      text-align: center;
      font-size: 15px;
      color: #444444;

      h4 {
        font-size: 24px;
        margin: 0 0 20px 0;
        padding: 0;
        line-height: 1;
        font-weight: 600;
        color: $primary;
      }

      form {
        margin-top: 30px;
        background: #fff;
        padding: 6px 10px;
        position: relative;
        border-radius: 50px;
        box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.06);
        text-align: left;

        input {
          &[type=email] {
            border: 0;
            padding: 4px 8px;
            width: calc(100% - 100px);
          }

          &[type=submit] {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            border: 0;
            background: none;
            font-size: 16px;
            padding: 0 20px;
            background: $secondary;
            color: #fff;
            transition: 0.3s;
            border-radius: 50px;
            box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);

            &:hover {
              background: darken($secondary, 5);
            }
          }
        }
      }
    }

    .footer-top {
      padding: 60px 0 30px 0;
      background: #fff;

      .footer-contact {
        margin-bottom: 30px;

        h3 {
          font-size: 28px;
          margin: 0 0 10px 0;
          padding: 2px 0 2px 0;
          line-height: 1;
          text-transform: uppercase;
          font-weight: 600;
          color: $primary;
        }

        p {
          font-size: 14px;
          line-height: 24px;
          margin-bottom: 0;
          font-family: "Jost", sans-serif;
          color: #5e5e5e;
        }
      }

      h4 {
        font-size: 16px;
        font-weight: bold;
        color: $primary;
        position: relative;
        padding-bottom: 12px;
      }

      .footer-links {
        margin-bottom: 30px;

        ul {
          list-style: none;
          padding: 0;
          margin: 0;

          i {
            padding-right: 2px;
            color: $secondary;
            font-size: 18px;
            line-height: 1;
          }

          li {
            padding: 10px 0;
            display: flex;
            align-items: center;

            &:first-child {
              padding-top: 0;
            }
          }

          a {
            color: #777777;
            transition: 0.3s;
            display: inline-block;
            line-height: 1;

            &:hover {
              text-decoration: none;
              color: $secondary;
            }
          }
        }
      }

      .social-links a {
        font-size: 18px;
        display: inline-block;
        background: $secondary;
        color: #fff;
        line-height: 1;
        padding: 8px 0;
        margin-right: 4px;
        border-radius: 50%;
        text-align: center;
        width: 36px;
        height: 36px;
        transition: 0.3s;

        &:hover {
          background: darken($secondary, 5);
          color: #fff;
          text-decoration: none;
        }
      }
    }

    .footer-bottom {
      padding-top: 30px;
      padding-bottom: 30px;
      color: #fff;
    }

    .copyright {
      float: left;
    }

    .credits {
      float: right;
      font-size: 13px;

      a {
        transition: 0.3s;
      }
    }
  }

  @media (max-width: 768px) {
    #footer {
      .footer-bottom {
        padding-top: 20px;
        padding-bottom: 20px;
      }

      .copyright {
        text-align: center;
        float: none;
      }

      .credits {
        text-align: center;
        float: none;
        padding-top: 4px;
      }
    }
  }
